@use "../abstracts" as a;
.main-header {
  width: 100%;
  max-width: 1920px;
  position: sticky;
  top: 0;
  padding: 20px 200px;
  @include a.flex(row, space-between, center);
  background-color: inherit;
  z-index: 2;
  @media (max-width: 1199px) {
    padding: 20px 32px;
  }
}

.blind {
  position: absolute;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header-login {
  width: 128px;
}

.sign-logo {
  width: 210px;
}

@media (max-width: 767px) {
  .main-header {
    padding: 13px 32px;

    .sign-logo {
      width: 88px;
    }

    .header-login {
      width: 80px;
    }
  }
}
